function doJson(data) {
  const logs = []
  const errorLogs = []
  const _logs = data.split('\n')
  for (let i = 0; i < _logs.length; i++) {
    const temp = _logs[i]
    try {
      logs.push(JSON.parse(temp.replaceAll(/^[^{]*?:({.*)/g, '$1')))
    } catch (error) {
      if (temp.trim() !== '') {
        console.warn('解析错误：非json，json内容[ ' + temp + ' ]')
        errorLogs.push({'line': i, 'log':temp})
      } else {
        console.warn('解析错误：空字符串')
      }
    }
  }
  return {"data": logs, "errorLogs": errorLogs}
}

function doLoki(data) {
  const logs = []
  const errorLogs = []
  const _logs = JSON.parse(data)
  for (let i = 0; i < _logs.length; i++) {
    const temp = _logs[i].line
    try {
      logs.push(JSON.parse(temp))
    } catch(error) {
      if (temp.trim() !== '') {
        console.warn('解析错误：非json，json内容[ ' + temp + ' ]')
        errorLogs.push({'line': i, 'log':temp})
      } else {
        console.warn('解析错误：空字符串')
      }
    }
  }
  return {"data": logs, "errorLogs": errorLogs}
}

// 数据分析
export function dataAnalysis(data, filter) {
  let logs = []
  const sortLogs = []
  const serverLogs = []
  const uniqueLogs = {}
  const uniqueLogsHit = {}
  const uniqueApps = {}
  const uniqueTrackIds = {}
  const sortLogsMap = {}
  const uniqueGroups = {}
  let errorLogs = []

  if (filter.logInputType == 1) {
    const ret = doJson(data)
    logs = ret.data
    errorLogs = ret.errorLogs
  } else if (filter.logInputType == 2) {
    const ret = doLoki(data)
    logs = ret.data
    errorLogs = ret.errorLogs
  }

  for (let i = 0; i < logs.length; i++) {
    const log = logs[i]
    try {
      if (log.type === 'PROTOCAL' && log.protocal.role === 'CLIENT') {
        log.protocal.role = 'SENDER'
      }
      if (log.type === 'PROTOCAL' && log.protocal.role === 'SERVER') {
        log.protocal.role = 'RECEIVER'
      }
      if (log.trackId === '') {
        log.trackId = '未填写'
      }
      // 跳过已经过滤掉的模块日志
      if (filter && Object.keys(filter.groups).length !== 0 && !filter.applications[log.application.name]) {
        continue
      }
      if (log.type === 'DEFAULT') {
        sortLogs.push(log)
        if (sortLogsMap[log.timestamp]) {
          sortLogsMap[log.timestamp].push(log)
        } else {
          sortLogsMap[log.timestamp] = [log]
        }
      } else if (log.type === 'PROTOCAL' && log.protocal.role === 'SENDER') {
        sortLogs.push(log)
        if (log.protocal.request.id) {
          uniqueLogs[log.protocal.request.id] = log.protocal.request.id
        }
        if (sortLogsMap[log.timestamp]) {
          sortLogsMap[log.timestamp].push(log)
        } else {
          sortLogsMap[log.timestamp] = [log]
        }
      } else if (log.type === 'PROTOCAL' && log.protocal.role === 'RECEIVER') {
        serverLogs.push(log)
        if (sortLogsMap[log.timestamp]) {
          sortLogsMap[log.timestamp].push(log)
        } else {
          sortLogsMap[log.timestamp] = [log]
        }
      }
      if (log.trackId) {
        uniqueTrackIds[log.trackId] = log.trackId
      }
      if (log.application.name) {
        uniqueApps[log.application.name] = log.application.name
      }

      if (log.protocal) {
        if (!log.protocal.source.group || !log.protocal.destination.group) {
          console.warn('group不存在：' + log + ' ]')
        }
        uniqueGroups[log.protocal.source.group] = log.protocal.source.group
        uniqueGroups[log.protocal.destination.group] = log.protocal.destination.group
      }
    } catch (error) {
      console.warn('处理错误：' + log + ' ]')
    }
  }
  for (let i = 0; i < serverLogs.length; i++) {
    const log = serverLogs[i]
    if (log.protocal.request.id) {
      if (uniqueLogs[log.protocal.request.id]) {
        uniqueLogsHit[log.protocal.request.id] = log.protocal.request.id
      } else {
        sortLogs.push(log)
      }
    } else {
      sortLogs.push(log)
    }
  }

  sortLogs.sort(function(a, b) {
    let time1 = new Date(a.timestamp).getTime()
    let time2 = new Date(b.timestamp).getTime()
    if (a.timestamp.length === 31) {
      time1 = time1 * 1000 + Number(a.timestamp.substring(23, 26))
      time2 = time2 * 1000 + Number(b.timestamp.substring(23, 26))
    }
    return time1 - time2
  })

  return {
    'sortLogs': sortLogs,
    'serverLogs': serverLogs,
    'uniqueLogs': uniqueLogs,
    'uniqueLogsHit': uniqueLogsHit,
    'uniqueTrackIds': uniqueTrackIds,
    'uniqueApps': uniqueApps,
    'sortLogsMap': sortLogsMap,
    'uniqueGroups': uniqueGroups,
    'errorLogs': errorLogs
  }
}

// 数据过滤
export function dataFilter(data, filter) {
  const filterLogs = []
  const filterGroups = {}
  for (let i = 0; i < data.sortLogs.length; i++) {
  //for (let i = filter.logRange[0]; i < filter.logRange[1]; i++) {
    const log = data.sortLogs[i]
    
    let contextReg, uriIncludeReg, uriExcludeReg
    if (filter && filter.contextReg && filter.contextReg.trim() !== '') {
      contextReg = new RegExp(filter.contextReg, 'gi')
    }
    if (filter && filter.uriIncludeReg && filter.uriIncludeReg.trim() !== '') {
      uriIncludeReg = new RegExp(filter.uriIncludeReg, 'gi')
    }
    if (filter && filter.uriExcludeReg && filter.uriExcludeReg.trim() !== '') {
      uriExcludeReg = new RegExp(filter.uriExcludeReg, 'gi')
    }

    if (!log) {
      break
    }
    if (contextReg && contextReg.exec(JSON.stringify(log)) == null) {
      continue
    }
    if (filter && Object.keys(filter.trackIds).length !== 0 && !filter.trackIds[log.trackId]) {
      continue
    }

    if (filter && Object.keys(filter.applications).length !== 0 && !filter.applications[log.application.name]) {
      continue
    }
    if (log.type === 'DEFAULT' && filter && !filter.logTypeDefault) {
      continue
    }
    if (log.type === 'PROTOCAL' && filter && !filter.logTypeProtocal) {
      continue
    }
    if (log.type === 'PROTOCAL') {
      if (filter && Object.keys(filter.groups).length !== 0 && (!filter.groups[log.protocal.source.group] || !filter.groups[log.protocal.destination.group])) {
        continue
      }

      if (uriIncludeReg && uriIncludeReg.exec(log.protocal.request.uri) == null) {
        continue
      }

      if (uriExcludeReg && uriExcludeReg.exec(log.protocal.request.uri) != null) {
        continue
      }
    }
    filterLogs.push(log)
    if (log.type === 'DEFAULT') {
      filterGroups[log.application.name] = log.application.name
    } else if (log.type === 'PROTOCAL') {
      filterGroups[log.protocal.source.group] = log.protocal.source.group
      filterGroups[log.protocal.destination.group] = log.protocal.destination.group
    }
  }

  return {"filterLogs": filterLogs, "filterGroups": filterGroups}
}

// 显示数据过滤
export function displayDataFilter(data, filter) {
  const filterLogs = []
  const filterGroups = {}
  for (let i = filter.logRange[0]; i < filter.logRange[1]; i++) {
    const log = data.filterLogs[i]
    filterLogs.push(log)
    if (log.type === 'DEFAULT') {
      filterGroups[log.application.name] = log.application.name
    } else if (log.type === 'PROTOCAL') {
      filterGroups[log.protocal.source.group] = log.protocal.source.group
      filterGroups[log.protocal.destination.group] = log.protocal.destination.group
    }
  }
  return {"filterLogs": filterLogs, "filterGroups": filterGroups}
}

// 图结构化数据生成
export function diagramDataGeneration(data, filterData, filter, document) {
  const mermaidBuffer = []
  mermaidBuffer.push('sequenceDiagram')
  mermaidBuffer.push('autonumber')
  //mermaidBuffer.push("%%{init: { 'sequence': {'messageAlign': 'left','noteAlign': 'left'} }}%%")
  //mermaidBuffer.push("%%{init:{'themeCSS':'.messageLine0:nth-of-type(2) { stroke: #f00; };.messageText:nth-of-type(1) { fill: green; font-size: 30px !important;}; g:nth-of-type(3) rect.actor { stroke:#00f;fill: #ff0; }; g:nth-of-type(5) .note { stroke:#00f;fill: #ff0; };#arrowhead path {stroke: blue; fill:#f00;};'}}%%")

  const highlightActor = {}
  for (let i = 0; i < filter.highlightLogProtocalGroup.length; i++) {
    highlightActor[filter.highlightLogProtocalGroup[i]] = filter.highlightColors[i]
  }

  let preProtocalLog
  let actorIndex = 0
  let lineIndex = 0
  let protocalIndex = 1
  let defaultIndex = Object.keys(filterData.filterGroups).length + 2
  const actor = {}
  let themeCSS = []

  for (let i = 0; i < filterData.filterLogs.length; i++) {
    const log = filterData.filterLogs[i]
  
    if (log.protocal && log.protocal.message) {
      log.protocal.message = log.protocal.message.split('\n')[0]
    }
    if (log.message) {
      log.message = log.message.split('\n')[0]
    }
    if (log.protocal && (!log.protocal.source.group || !log.protocal.destination.group)) {
      console.warn('非法日志:' + JSON.stringify(log))
      continue
    }

    let highlightContextReg, highlightContext = false
    if (filter && filter.highlightContextRegular && filter.highlightContextRegular.trim() !== '') {
      highlightContextReg = new RegExp(filter.highlightContextRegular, 'gi')
    }

    if (highlightContextReg && highlightContextReg.exec(JSON.stringify(log)) != null) {
      highlightContext = true
    }

    if (log.type === 'DEFAULT') {
      if (highlightContext) {
        themeCSS.push(addFilterDefaultHighlightCss(defaultIndex))
      }
      defaultIndex = defaultIndex + 1

      if (!actor[log.application.name]) {
        actorIndex = actorIndex + 1
        actor[log.application.name] = log.application.name
        if (highlightActor[log.application.name]) {
          themeCSS.push(addActorHighlightCss(actorIndex, Object.keys(filterData.filterGroups).length, highlightActor[log.application.name]))
        }
      }
      mermaidBuffer.push('Note over [' + log.application.name + ']:(' + i + ')' + log.timestamp + '<br/>' + (log.message.length > 100 ? mermaidEncode(log.message.substring(0, 100)) + '...' : mermaidEncode(log.message)))
    } else if (log.type === 'PROTOCAL') {
      if (!actor[log.protocal.source.group]) {
        actorIndex = actorIndex + 1
        actor[log.protocal.source.group] = log.protocal.source.group
        if (highlightActor[log.protocal.source.group]) {
          themeCSS.push(addActorHighlightCss(actorIndex, Object.keys(filterData.filterGroups).length, highlightActor[log.protocal.source.group]))
        }
      }
      if (!actor[log.protocal.destination.group]) {
        actorIndex = actorIndex + 1
        actor[log.protocal.destination.group] = log.protocal.destination.group
        if (highlightActor[log.protocal.destination.group]) {
          themeCSS.push(addActorHighlightCss(actorIndex, Object.keys(filterData.filterGroups).length, highlightActor[log.protocal.destination.group]))
        }
      }

      lineIndex = lineIndex + 1
      if (highlightActor[log.protocal.source.group]) {
        themeCSS.push(addLineHighlightCss(lineIndex, highlightActor[log.protocal.source.group]))
      }

      let role = log.protocal.role
      if (log.protocal.role === 'SENDER' && data.uniqueLogsHit[log.protocal.request.id]) {
        role = 'RECEIVER,SENDER'
      }
      role = role + '|' + i

      let prefix = log.protocal.type

      let preProtocalDuration = 0
      let highlightPreProtocalDuration = false
      if(preProtocalLog) {
        let tempTime1 = new Date(log.timestamp).getTime()
        let tempTime2 = new Date(preProtocalLog.timestamp).getTime()
        preProtocalDuration = tempTime1 - tempTime2
        if (log.timestamp.length === 31) {
          tempTime1 = tempTime1 * 1000 + Number(log.timestamp.substring(23, 26))
          tempTime2 = tempTime2 * 1000 + Number(preProtocalLog.timestamp.substring(23, 26))
          preProtocalDuration = (tempTime1 - tempTime2) / 1000
        }
        if (filter.highlightPreProtocalDuration > 0 && filter.highlightPreProtocalDuration < preProtocalDuration) {
          highlightPreProtocalDuration = true
        }
      }
      preProtocalLog = log

      let highlightProtocalDuration = false
      if (log.protocal.duration) {
        let logProtocalDuration = log.protocal.duration
        if (log.protocal.request.timestamp.length === 31) {
          logProtocalDuration = log.protocal.duration / 1000
        }
        
        prefix = prefix + ',' + formatDuration(logProtocalDuration) + ',↑' + formatDuration(preProtocalDuration)
        if (filter.highlightProtocalDuration > 0 && filter.highlightProtocalDuration < logProtocalDuration) {
          highlightProtocalDuration = true
        }
      } else {
        prefix = prefix + ',↑' + formatDuration(preProtocalDuration)
      }

      let context = ''
      if (filter.displayMode === '2') {
        if (log.labels && log.labels.code) {
          context = log.labels.code
        } else {
          if (log.protocal.request.method) {
            context = '(' + log.protocal.request.method + ')' + log.protocal.request.uri
          } else {
            context = log.protocal.request.uri
          }
        }
      } else {
        if (log.protocal.request.method) {
          context = '(' + log.protocal.request.method + ')' + log.protocal.request.uri
        } else {
          context = log.protocal.request.uri
        }
      }
      context = mermaidEncode(context)
      prefix = mermaidEncode(prefix)

      if (log.protocal.code === 'SUCCESS') {
        if (highlightContext) {
          themeCSS.push(addFilterProtocalHighlightCss(protocalIndex, 3))
        }
        protocalIndex = protocalIndex + 3
        if (highlightPreProtocalDuration) {
          mermaidBuffer.push('rect rgb(255, 153, 51, 0.4)')
        }
        if (highlightProtocalDuration) {
          mermaidBuffer.push('rect rgb(255, 255, 0, 0.4)')
        }
        mermaidBuffer.push('[' + mermaidEncode(log.protocal.source.group) + ']' + ' ->> ' + '[' + mermaidEncode(log.protocal.destination.group) + ']' + ':(' + role + ')' + log.timestamp + '<br/>[' + prefix + ']' + context)
        if (highlightProtocalDuration) {
          mermaidBuffer.push('end')
        }
        if (highlightPreProtocalDuration) {
          mermaidBuffer.push('end')
        }
      } else {
        if (highlightContext) {
          themeCSS.push(addFilterProtocalHighlightCss(protocalIndex, 4))
        }
        protocalIndex = protocalIndex + 4
        if (highlightPreProtocalDuration) {
          mermaidBuffer.push('rect rgb(255, 153, 51, 0.4)')
        }
        if (highlightProtocalDuration) {
          mermaidBuffer.push('rect rgb(255, 255, 0, 0.4)')
        }
        mermaidBuffer.push('rect rgb(255, 0, 0, 0.5)')
        mermaidBuffer.push('[' + mermaidEncode(log.protocal.source.group) + ']' + ' -x ' + '[' + mermaidEncode(log.protocal.destination.group) + ']' + ':(' + role + ')' + log.timestamp + '<br/>[' + prefix + ']' + context + '<br/>' + mermaidEncode(log.protocal.code) + ': ' + mermaidEncode(log.protocal.message))
        mermaidBuffer.push('end')
        if (highlightProtocalDuration) {
          mermaidBuffer.push('end')
        }
        if (highlightPreProtocalDuration) {
          mermaidBuffer.push('end')
        }
      }
    }
  }

  //mermaid在初始化themeCss的时候，如果携带>或者;等特殊符号会出现css不完整的情况
  //mermaidBuffer.push("%%{init:{'themeCSS':'" + themeCSS.join('') + "'}}%%")
  const cssDom = document.getElementById('mermaidCss')
  if (cssDom) {
    cssDom.innerHTML = themeCSS.join('')
  } else {
    const dom = document.createElement('style');
    dom.id = 'mermaidCss';
    dom.type = 'text/css';
    dom.innerHTML = themeCSS.join('')
    document.head.appendChild(dom);
  }

  if (mermaidBuffer.length < 3) {
    return
  }
  console.info(mermaidBuffer.join('\n'))
  return mermaidBuffer.join('\n')
}

function mermaidEncode(str) {
  if(!str) {
    return ''
  }
  return str.replaceAll('#', '%%36%%').replaceAll(';', '#59;').replaceAll('+', '#43;').replaceAll(':', '#58;').replaceAll('>', '#62;').replaceAll('<', '#60;').replaceAll('%%36%%', '#36;')
}

function addLineHighlightCss(index, color) {
  const css = []
  css.push('.svg-pan-zoom_viewport>line:nth-of-type(')
  css.push(index)
  css.push('){stroke:')
  css.push(color.backgroundColor)
  css.push('!important;}')
  return css.join('')
}

function addFilterDefaultHighlightCss(index) {
  const css = []
  css.push('.svg-pan-zoom_viewport>g:nth-of-type(')
  css.push(index)
  css.push(') tspan{animation:highlight 1s infinite alternate;}')
  return css.join('')
}


function addFilterProtocalHighlightCss(index, count) {
  const css = []
  for(let i=0; i < count; i++) {
    css.push('.svg-pan-zoom_viewport>text:nth-of-type(')
    css.push(index)
    css.push('){animation:highlight 1s infinite alternate;}')
    index = index + 1
  }
  return css.join('')
}

function addActorHighlightCss(index, count, color) {
  const css = []
  // css中>指的只选择子节点，而不选择子节点的子节点
  // actor背景色
  css.push('.svg-pan-zoom_viewport>g:nth-of-type(')
  css.push(index + 1)
  css.push(') g rect.actor{fill:')
  css.push(color.backgroundColor)
  css.push('!important;stroke:')
  css.push('rgba(255, 255, 255, 1)')
  css.push('!important;}')
  css.push('.svg-pan-zoom_viewport>g:nth-last-of-type(')
  css.push(count - (index - 1))
  css.push(') rect.actor{fill:')
  css.push(color.backgroundColor)
  css.push('!important;stroke:')
  css.push('rgba(255, 255, 255, 1)')
  css.push('!important;}')
  // actor字体颜色
  css.push('.svg-pan-zoom_viewport>g:nth-of-type(')
  css.push(index + 1)
  css.push(') g text tspan{fill:')
  css.push(color.fontColor)
  css.push('!important;}')
  css.push('.svg-pan-zoom_viewport>g:nth-last-of-type(')
  css.push(count - (index - 1))
  css.push(') text tspan{fill:')
  css.push(color.fontColor)
  css.push('!important;}')
  // line背景色
  css.push('.svg-pan-zoom_viewport>g:nth-of-type(')
  css.push(index + 1)
  css.push(') line{stroke:')
  css.push(color.backgroundColor)
  css.push('!important;}')
  return css.join('')
}

function formatDuration(milliseconds) {
  if (milliseconds <= 0) {
    return '0μs'
  }

  // 格式化的字符串数组
  const formatArr = []
  // 定义临时的余数和整数
  let n = 0, r = 0
  // 获得微妙，使用toFixed(0)解决8.139*1000等于8138.999999999999的精度问题
  let time = (milliseconds * 1000).toFixed(0)
  // 微妙
  n = Math.floor(time / 1000)
  r = time % 1000
  time = n
  if (r != 0) {
    formatArr.push(r + 'μs')
  }
  if (n == 0) {
    return formatArr.join('')
  }
  // 毫秒
  n = Math.floor(time / 1000)
  r = time % 1000
  time = n
  if (r != 0) {
    formatArr.push(r + 'ms')
  }
  if (n == 0) {
    formatArr.reverse()
    return formatArr.join('')
  }
  // 秒
  n = Math.floor(time / 60)
  r = time % 60
  time = n
  if (r != 0) {
    formatArr.push(r + 's')
  }
  if (n == 0) {
    formatArr.reverse()
    return formatArr.join('')
  }
  // 分钟
  n = Math.floor(time / 60)
  r = time % 60
  time = n
  if (r != 0) {
    formatArr.push(r + 'm')
  }
  if (n == 0) {
    formatArr.reverse()
    return formatArr.join('')
  }
  // 小时
  formatArr.join(n + 'h')

  formatArr.reverse()
  return formatArr.join('');
}